nav {
    width: 100%;
    height: 70px;
    background-color: white;
    box-shadow: 0px 3px 7.5px rgba(0, 0, 0, 0.1);
    border-radius: 0px 0px 9px 9px;
}

.nav--logo{
    width: 82px;
    height: 25px;
    margin: 22px 0 0 30px;
}

.nav--logo:hover{
    cursor: pointer;
}

@media screen and (max-width:475px) {
   nav{
    height: 50px;
   }
   .nav--logo{
    width: 82px;
    height: 25px;
    margin: 12.5px 17.5px;
}
}

@media screen and (min-width:1200px) {
    nav{
     height: 100px;
    }
    .nav--logo{
     width: 164px;
     height: 50px;
     margin: 25px 35px;
 }
 }