/* BOX QUE CONTÃ‰M TODO O BANNER */

.banner--container{
    display: inline-block;
    width: 15vw;
    height: 400px;
    margin-left: 20px;
    margin-bottom: 20px;
    vertical-align: middle;
}

/* FOTO E BATCH  */

.banner--photo--container{
    position: relative;
}


.banner--photo{
    width: 100%; 
}

.banner--batch{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 5vw;
    height: 1.3vw;
    border-radius: 5px;
    font-size: 0.9vw;
    background-color: #ffffff;
    position: absolute;
    top: 10px;
    left: 10px;
    font-weight: normal;
}

/* TEXTO ABAIXO DA FOTO */

.banner--text--container{
    padding-left: 5px;
    font-size: 0.9vw;
}

.banner--rating--container{
    display: flex;
    gap: 4px;
}

.banner--rating--star{
    width: 15px;
}

.banner--rating--number{
    color: #918E9B;
}

.banner--price{
    font-weight: bolder;
}

.nowrap{
    white-space: nowrap;
}

.banner--description{
    white-space: normal;
}

@media screen and (max-width:1350px) {
    .banner--batch{
        width: 6vw;
        height: 1.4vw;
        border-radius: 3px;
        font-size: 1.05vw;
        top: 10px;
        left: 10px;
        font-weight: normal; 
        border: 1px solid black;
        
    }
    .banner--text--container{
        font-size: 14px;
    }
}

@media screen and (max-width:810px) {
    .banner--container{
        width: 30vw;
    }
    .banner--batch{
        width: 12vw;
        height: 2.8vw;
        border-radius: 4px;
        font-size: 2.1vw;
        top: 10px;
        left: 10px;
        font-weight: normal; 
        border: 1px solid black;
        
    }
}

@media screen and (max-width:475px) {
    .banner--container{
        width: 30vw;
    }
    .banner--batch{
        width: 20vw;
        height: 4vw;
        border-radius: 4px;
        font-size: 2.8vw;
        top: 5px;
        left: 50%;
        transform: translate(-50%,0);
        font-weight: normal; 
        border: 1px solid black;
        
    }
    .banner--text--container{
        font-size: 10px;
    }
}
